<template>
  <div class="home">
     <div class="header">
       <el-menu
         :default-active="activeIndex2"
         class="el-menu-demo"
         mode="horizontal"
         background-color="#545c64"
         text-color="#fff"
         active-text-color="#ffd04b">
         <el-menu-item index="1">other</el-menu-item>
         <el-menu-item index="2">home</el-menu-item>
         <el-menu-item index="3">other</el-menu-item>
         <el-menu-item index="4">message</el-menu-item>
         <el-menu-item index="6">mess</el-menu-item>
         <el-menu-item index="5"><el-button @click="closeMenu" type="danger">OpenMenu</el-button></el-menu-item>
       </el-menu>
     </div>
     <div class="centers">
       <template>
         <el-carousel :interval="2000" type="card" height="200px">
           <el-carousel-item v-for="item in 6" :key="item">
             <h1>{{item}}</h1>
           </el-carousel-item>
         </el-carousel>
       </template>
     </div>
     <div class="bottoms">
       <div class="block">
         <span class="demonstration">有默认值</span>
         <el-color-picker v-model="color1"></el-color-picker>
       </div>
       <div class="block">
         <span class="demonstration">无默认值</span>
         <el-color-picker v-model="color2"></el-color-picker>
       </div>
     </div>
  </div>
</template>

<script>
  export default {
    data() {
      const imgs =[
          '../../assets/img/img1.jpg',
          '../../assets/img/img2.jpg',
          '../../assets/img/img3.jpg',
          '../../assets/img/img4.png',
      ]
      return {
        activeIndex2: '0',
        color1: '#409EFF',
        color2: null,
        imgs
      }
    },
    methods: {
      closeMenu(){
        this.$store.commit('SET_OPEN_SIDERmENU', true)
      }
    }
  }
</script>

<style rel="stylesheet/scss"  lang="scss" scoped>
  .centers{
    .el-carousel__item h3 {
      color: #475669;
      font-size: 14px;
      opacity: 0.75;
      line-height: 200px;
      margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
      background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n+1) {
      background-color: #d3dce6;
    }
    .bottoms{
      margin-top: 100px;
    }
  }
</style>



