<template>
  <div id="app">
    <sider-bar v-show="open"/>
    <router-view/>
  </div>
</template>

<script>
  import {mapGetters, mapState} from 'vuex'
  import siderBar from '@/components/siderbar'
  export default {
    name: 'App',
    components: {siderBar},
    computed: {
      ...mapGetters(['openSiderMenu']),
      ...mapState({
        open: state => state.app.openSiderMenu
      }),
      // open() {
      //   return this.$store.getters.openSiderMenu
      // }
    },
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    /*margin-top: 60px;*/
  }
</style>



