<template>
  <div class="login">
    <div class="logo">
      <img src="../../assets/logo.png">
    </div>
    <el-form :model="numberValidateForm" ref="numberValidateForm" label-width="40px" class="demo-ruleForm">
      <el-form-item
        label="年龄"
        prop="age"
        :rules="[
      { required: true, message: '年龄不能为空'},
      { type: 'number', message: '年龄必须为数字值'}
    ]"
      >
        <el-input type="age" v-model.number="numberValidateForm.age" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="年龄">
        <el-input v-model.number="numberValidateForm.age1"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('numberValidateForm')">提交</el-button>
        <el-button @click="resetForm('numberValidateForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        numberValidateForm: {
          age: '',
          age1: ''
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$router.push('/')
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style scoped rel="stylesheet/scss" lang=scss>
  .login{
    position: fixed;
    top:0;
    left:0;
    padding-top: 100px;
    width:100%;
    height: 100%;
    background-image: url(../../assets/img/trans-to-bg.jpg);
    .logo{
      width:100%;
      img{
        /*display:block;*/
        margin: 0 auto;
      }
    }
    .demo-ruleForm{
      width:80%;

    }
  }

</style>



