import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/views/home'
import login from '@/views/login'
import sweep from '@/views/sweep'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/sweep',
      name: 'sweep',
      component: sweep
    }
  ]
})



