<template>
  <div class="sider-bar sider-bar-main">
    <div class="header-message">
      <div class="logo">
        <img src="../../assets/img/gailun.png" class="logo-img"/>
        <span class="name">Cat noote</span>
        <span class="other">lib for mygithub</span>
        <button class="menu-btn" type="button" @click="closeSiderMenu">|||</button>
      </div>
      <div class="logo-some">
        <span class="some one">
          <span class="svg">1</span>
          <span class="tip">2</span>
          <span class="desc">3</span>
        </span>
        <span class="some"></span>
        <span class="some"></span>
        <span class="some"></span>
        <span class="some"></span>
      </div>
    </div>
    <div class="body-content">
      some
    </div>
    <div class="bottom-other">
      <button class="btn" type="button">LOGIN OUT</button>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'sider-menu',
    methods:{
      closeSiderMenu(){
        this.$store.commit('SET_OPEN_SIDERmENU', false)
      }
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .sider-bar-main {
    padding-left: 20px;
    width: 80%;
    background-image: url(../../assets/img/trans-to-bg.jpg);
    .header-message {
      padding-top: 10px;
      width: 100%;
      .logo {
        margin-bottom: 10px;
        padding: 10px 0;
        width: 100%;
        height: 80px;
        position: relative;
        padding-left: 95px;
        padding-right: 80px;
        text-align: left;
        .logo-img {
          position: absolute;
          left: 0;
          top: 0;
          width: 80px;
          height: 80px;
          border-radius: 50%;
        }
        .name {
          display: block;
          width: 100%;
          height: 35px;
          line-height: 25px;
          font-size: 20px;
          color: #ffffff;
        }
        .other {
          display: block;
          width: 100%;
          height: 25px;
          line-height: 25px;
          font-size: 15px;
          color: #c2c0be;
        }
        .menu-btn{
          position: absolute;
          right:0;
          top:0;
          width: 80px;
          height: 80px;
          background: transparent;
          outline: none;
          color: #ffffff;
          font-size: 60px;
          border:none
        }
      }
      .logo-some {
        width: 100%;
        height: 80px;
        display: flex;
        border-bottom: 1px solid #fff;
        .some {
          flex: 1;
          height: 80px;
          border-left: 1px dashed #fff;
          .desc,
          .svg {
            display: block;
            height: 30px;
            line-height: 35px;
            font-size: 20px;
            color: #fff;
          }
          .tip {
            display: block;
            height: 20px;
            line-height: 25px;
            font-size: 15px;
            color: #efefef;
          }
        }
      }
      .some.one {
        border: 0;
      }
    }

    .bottom-other {
      position: absolute;
      bottom: 10px;
      width: 100%;
      font-size: 25px;
      color: #ffffff;
      button{
        display: block;
        height: 50px;
        width: 60%;
        margin: 0 auto;
        background: transparent;
        border: 1px solid #fff;
        border-radius: 30px;
        line-height: 48px;
        outline: none;
        color:#fff;
      }
    }
  }
</style>



